
.c-reddot {
  position: relative
}
.c-reddot::after {
  content: ' ';
  position: absolute;
  background-color: #F43531;
  border-radius: 50%
}
[data-dpr="1"] .c-reddot::after {
  right: -3px;
  top: -3px;
  width: 6px;
  height: 6px
}
[data-dpr="2"] .c-reddot::after {
  right: -6px;
  top: -6px;
  width: 12px;
  height: 12px
}
[data-dpr="3"] .c-reddot::after {
  right: -9px;
  top: -9px;
  width: 18px;
  height: 18px
}



